* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --font-color-light: #f8f9fd;
  --font-color-unselected: var(--clr-grey-50);
  --font-color-dark: var(--clr-grey-900);
  --font-color-tint1: var(--clr-grey-600);
  --font-color-tint2: var(--clr-grey-400);
  --border-color: var(--clr-grey-40);
  --bg-color: #000;
  --secondary-color: #1f1f1f;
  --secondary-color-a6: rgba(31, 31, 31, 0.6);
  --card-color: #2e2e2e;
  --card-color-a55: rgba(46, 46, 46, 0.55);
  --light-card-color: #f2f2f2;
  --quaternary-color: #333333;
  --hover-color: #363636;
  --primary-color: #1d9bf0;
  --danger-color: #ff0000;
  --warning-color: #fade53;
  --light-shadow: 0 5px 15px rgba(165, 165, 165, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 1);
  --transition-one: all 0.4s linear;
  --transition-two: 0.3s ease-in-out all;
  --borderRadius: 10px;
  --letterSpacing: 1px;
  --max-width: 1250px;
  --fixed-width: 500px;
  --fluid-width: 90vw;
}
@media (max-width: 800px) {
  html {
    font-size: 90%;
  }
}
a {
  background: transparent;
  text-decoration: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
body {
  font-family: “Roboto”, Arial, Helvetica, sans-serif, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--bg-color);
  color: var(--font-color-light);
}
.container {
  display: flex;
  height: 100%;
  max-width: var(--max-width);
  margin: auto;
}
