::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background-color: var(--bg-color);
  -webkit-border-radius: 10px;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
  border-radius: 10px;
  background: var(--font-color-light);
}
.flex-center-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.loading-component {
  margin: 2rem 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}
.hover-animation {
  width: auto;
  height: auto;
  cursor: pointer;
  border-radius: 100%;
  padding: 0.75rem 1rem;
  transition: 0.2s ease-out;
}
.hover-animation:hover {
  background: #d9d9d9;
  opacity: 0.1;
}
.icon-action {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  border-radius: 10px;
  padding: 0 0.5rem;
  transition: ease-out;
}
.icon-action:hover {
  background: hsla(204, 88%, 53%, 0.2);
}
@media screen and (max-width: 1280px) {
  .hover-animation {
    width: 3.25rem;
    height: 3.25rem;
    padding: 0;
  }
}
.main-container {
  flex: 1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  border-left: 1px solid var(--font-color-tint2);
  border-right: 1px solid var(--font-color-tint2);
  margin: 0 2rem;
  padding: 0 0.5rem;
}
.avatar-standard {
  width: 2.3rem;
  height: 2.3rem;
}
.avatar-small {
  width: 1.8rem;
  height: 1.8rem;
}
.menu {
  margin-left: auto;
}
.menu-btn {
  margin-top: 6px;
  font-size: 1.1rem;
  cursor: pointer;
  margin-left: auto;
}
.dropdown {
  position: absolute;
  right: 1.5rem;
  top: 2.5rem;
  z-index: 2;
  background: var(--secondary-color);
}
.item {
  border: none;
  gap: 5px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.item:hover {
  background: var(--hover-color);
}
.title {
  padding: 0.5rem;
  position: sticky;
  top: 0;
  background: rgba(0, 0, 0, 0.85);
  z-index: 10;
}
@media screen and (max-width: 1100px) {
  .main-container {
    margin: 0 1rem;
  }
}
@media screen and (max-width: 950px) {
  .main-container {
    border-right: none;
  }
}
@media screen and (max-width: 600px) {
  .main-container {
    margin: 0 0.5rem;
  }
}
.react-loading-skeleton {
  background: #2e2e2e;
  box-shadow: inset 4px 4px 6px var(--secondary-color),
    inset -4px -4px 6px var(--secondary-color);
}
.react-loading-skeleton::after {
  background-image: linear-gradient(
    90deg,
    var(--card-color),
    rgba(29, 156, 240, 0.776),
    var(--card-color)
  );
}

